/**
 * Demo Styles
 */
 

 html {
    height: 100%;
    box-sizing: border-box;
  }
  
  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }
  
  body {
    position: relative;
    margin: 0;
    min-height: 100%;
    height: 130vh;
    font-family: "Helvetica Neue", Arial, sans-serif;
  }
  
  .demo {
    margin: 0 auto;
    padding-top: 64px;
    max-width: 640px;
    width: 94%;
  }
  
  .demo h1 {
    margin-top: 0;
  }
  
  /**
   * Footer Styles
   */
  
