.EventsWrapper {
    height: 100%;
    margin-left: 30%;
    padding-top : 10%;
    width: 40%;
}

.EventWrapper {
    display: flex;
    height: 110vh;
    width: 100%;
    padding: 0;
}

.EventsInfo th, td{
    width:150px; 
    height: 5vh; 
}

.LeftInfoBlockEvent {
    width: 50%;
    height: 100%;
}

.LeftInfoBlockEvent img{
     display: block; 
     padding-top: 12vh;     
     margin-left: 50vh; 
    width: 300px;
    height: 300px;
    /* float: left; */
}

.RightInfoBlockEvent {
    padding-top: 10vh;
    width: 50%;
    height: 100%;
}

.RightInfoBlockEvent h1{
    text-align: left;
    font-family: 'Lobster', cursive;
    font-size: 300%;
}

.RightInfoBlockEvent p{
    text-align: left;
    font-family: 'Solway', cursive;
    font-size: 150%;
    max-width: 80%;
}

.RightInfoBlockEvent textarea { 
    height: 30vh;
    max-width: 80%;
    max-height: 50vh;
    text-align: center;
    font-family: 'Solway', cursive;
    font-size: 120%;
}

.RightInfoBlockEvent button{
    margin-top: 3vh;
    width : 15%;
    height: auto;
}

.NameInfo {
    display: flex;
    font-family: 'Solway', cursive;
    font-size: 150%;
    max-width: 80%;
    padding-top: 3vh;
    padding-left: 50%;
}

.DifficultyInfo {
    display: flex;
    font-family: 'Solway', cursive;
    font-size: 140%;    
    width: 80%;
    padding-left: 50%;
}

.StatusInfo {
    display: flex;
    font-family: 'Solway', cursive;
    font-size: 140%;
    max-width: 70%;
    padding-left: 50%;
    width: 90%;
}

.StatusInfo select{
    min-width: 83%;
}

.EventChangeTheme {
    padding-top: 1vh;
    display: flex;
    max-width: 70%;
    padding-left: 50%;
}




.DeadlineInfo {
    margin-top: 2vh;
    display: flex;
    max-width: 90%;
    max-height: 10px;
    padding-left: 50%;
    width: 100%;
}

.datetime {
    display: inline-flex;
}

.rdtCounter {
    display: flex;
}

.rdtCounterSeparator {
    display: none;
}

#date {
    font-family: 'Solway', cursive;
    font-size: 140%;
}

.DeadlineInfo p{
    font-family: 'Solway', cursive;
    font-size: 140%;
}

.AddButton {
    display: flex;
    padding-left: 33%;
    width : 60%;
    margin-top:20px;
}

.AddEventWrapper {
    display: flex;
    height: 100%;
    width: 100%;
    padding: 0;
}

.Input {
    width: 40vh;
    margin-top:  10vh;
    margin-left: 80vh;
}

.Input label ,button{
    margin-top: 10px;
}

.DeleteButton {
    margin-top: 3vh;
    padding-left: 50%;
    width : 60%;
}

.Search{    
    width: 30%;
    margin-left: 10vh;
}

.SearchBlock {
    margin-bottom: 2vh;
    display: flex;
}

.SearchBlock label{
    margin-top: 0.4vh;
    font-size: 140%;
}

.NothingFoundCol {
    margin-left: 20%;
    font-family: 'Solway', cursive;
    font-size: 200%;
}

.sortCheckBoxes label{
    font-size: 110%;
}

.sortCheckBoxes label{
    padding-left: 40px;
}
