
    .topnavbar {
        background-color: #333;
        position: fixed;
        top: 0;
        width: 100%;
        overflow: hidden;
        height: 50px;   
        font-family: Arial, Helvetica, sans-serif;
        z-index:1;
}

    body {
        margin: 0;
    }
  
    .topnavbar a {
        float: right;
        color: #f2f2f2;
        padding: 10px 14px 16px 10px;
        text-decoration: none;
        font-size: 20px;
    
}
  
    .topnavbar a:hover {
        background-color: #ddd;
        color: black;
}

.img {
    float: left;
    position: relative;
    width: 140px;
    height: 40px;
    margin-top: 5px;
    margin-left: 5px;
    z-index:2;

}