.ProfileWrapper {
    position: relative;
    display: flex;
    min-height: 100%;
    height: auto;
    width: 100%;
    padding: 0;
}

.ProfileLeftBlock {
    width: 30%;
    height: 100%;
    min-height: 100%;
}

.ProfileLeftBlock img{
    padding-left: 8vh;
    padding-top: 11vh;
    width:400px;
    height: 330px;
}

.ProfileRightBlock {
    width: 70%;
    height: 100%;
    min-height: 100%;
}

.ProfileRightBlock h1{
    padding-top: 8vh;
    text-align: center;
    font-family: 'Lobster', cursive;
    font-size: 300%;
}

.ProfileRightBlock p{
    text-align: center;
    padding-right: 5vh;
    font-family: 'Solway', cursive;
    font-size: 120%;
}


.ProfileRightBlock textarea { 
    height: 40vh;
    max-width: 90%;
    max-height: 50vh;
    text-align: center;
    font-family: 'Solway', cursive;
    font-size: 120%;
}

.ProfileRightBlock button{
    display: block;
    width: 90px;
    height: 40px;
    margin-top: 3vh;
}

.FullnameInfo {
    display: flex;
    text-align: center;
    margin-top: 2vh;
    font-family: 'Solway', cursive;
    font-size: 120%;
    max-width: 60%;
    padding-left: 8vh;
}

.ErrorFullname {
    padding-left: 20vh;
}

.StatusInfo1 {
    display: flex;
    text-align: center;
    font-family: 'Solway', cursive;
    font-size: 120%;
    max-width: 60%;
    padding-left: 8vh;
}

.StatusInfo p{
    min-width: 30%;
    margin-left : 1.5vh;
}

.StatusInfo select{    
    max-width: 40%;
    margin-left: 3.2vh;
}

.Telegraminfo {
    display: flex;
    text-align: center;
    font-family: 'Solway', cursive;
    font-size: 120%;
    max-width: 60%;
    padding-left: 8vh;
}

.ProfileChangeAva {
    margin-top: 3vh;
    display: flex;
    text-align: center;
    font-family: 'Solway', cursive;
    font-size: 120%;
    max-width: 60%;
    padding-left: 8vh;
}

.ProfileChangeAva input[type=file]{
    min-width: 30vh;
}