.CalendarAddStyle{
    margin-top: 20vh; 
}

.CalendarWrapper {
    display: flex;
    height: 100%;
    width: 100%;
    padding: 0;
}

.CalendarLeftBlock {
    padding-top: 10vh;
    width: 40%;
    height: 100%;
    padding-left: 25%;
}

.CalendarLeftBlock img{
    width: 300px;
    height: 250px;
}

.CalendarRighttBlock {
    display: block;
    padding-top: 10vh;
    width: 1000px;
    height: 100%;
    padding-left: 5%;
}

.CalendarRighttBlock label{
    text-align: left;
    font-family: 'Lobster', cursive;
    font-size: 230%;
}

.CalendarRighttBlock p, .CalendarRighttBlock h4{
    text-align: left;
    font-size: 240%;
    font-family: 'Solway', cursive;
}

.CalendarRighttBlockP{
    padding-top: 0.7vh;
}

.NameInfoClP {
    font-size: 80%;
}


.NameInfoCl {
    max-height: 10vh;
    display: inline-flex;
    max-width: 100%;
}

.CalendarEvents {
    width: 45%;
}

.NotCalendarsEvents {
    
    width: 45%;
}

.prevButtonClEv {
    width: 12%;
}

.nextButtonClEv {
    width: 12%;
}

.EventsButtonsCl{
    display: flex;
}

.EventsButtonsCl p{
    margin-top: 2vh;
    margin-left: 2vh;
    margin-right: 2vh;
    font-family: 'Courier New', Courier, monospace;
    font-size: 100%;
}

.RightBlock2 {
    float:left;
    margin-top:  2vh;
    display: block;
    padding-top: 10vh;
    width: 60%;
    height: 100%;
}

.NotCalendarEvents {
    width: 40%;
}

.NotCalendarEvents h4 {
    text-align: left;
    font-size: 230%;
    font-family: 'Solway', cursive;
}

.PagBut2 {
    display: flex;
    width: 85%;
}

.PagBut2 p{
    margin-top: 2vh;
    margin-left: 2vh;
    margin-right: 2vh;
    font-family: 'Courier New', Courier, monospace;
    font-size: 100%;
}

.AddLink {
    margin-top: 3vh;
    width: 25%;
}

.AddButtonCL {
    width: 18%;
}

.DeleteLink {
    margin-top: 3vh;
    width: 30%;
}

.AddButtonCL2{
    width: 22%;
}

.DeleteButtonCl {
    margin-top: 3vh; 
}

.EditMode {
    position: absolute;
    top : 55%;
    left : 45%;     
    width: 5%;
    height: 5%;
}

.NameInfoCl input[type="text"]{
    font-size:24px;
}

.CalendarLeftBlock input[type="file"]{
    margin-top:2vh;
}


.NothingFoundProfile {
    font-size: 150%;
}