

.WrapperHome {
    position: relative;
    min-height: 100%;
    height: auto;
    width: 100%;
    padding: 0;
}

.FirstInfoBlock {
    display: flex;
    width : 100%;
    background: #F5F5DC;
    height : 80vh;
}

.FirstInfoBlockText {
    padding-top: 8vh;
    padding-left: 20vh;
    width : 60%;
}

.FirstInfoBlockText h1{
    padding-left: 10vh;
    font-family: 'Lobster', cursive;
    font-size: 280%;
}

.FirstInfoBlockText p{
    padding-left: 10vh;
    padding-top: 4vh;
    font-family: 'Solway', cursive;
    font-size: 200%;
}

.FirstInfoBlockImg{
    padding-top: 8vh;
    width : 40%;
}

.FirstInfoBlockImg img{
    float: right;
    padding-right: 15vh;
    height: 500px;
    width: 500px;
}

.SignUpLink {
    margin-left: 10vh;
    font-size: 200%;
}


@import url('https://fonts.googleapis.com/css?family=Alata|Lobster|Solway&display=swap');